@import url(https://fonts.googleapis.com/css?family=Nunito);

*{
  background-color: #fff;
  padding: 0;
  margin: 0;
  font-family: "Nunito";
}

.benj{
  font-size: 3em;
  font-weight: 700;
  letter-spacing: 0.07rem;
}

.skill{
  font-size: 2em;
  font-weight: 500;
  letter-spacing: 0.07rem;
}

.hire-me{
  background-color: #f8a710 !important;
  border: 1px solid #f5a209;
  color: #fff;
  border-radius: calc(45px/2);
  height: 45px;
  font-size: 1.2em;
}

.hire-me:hover{
  color: #fff;
  border: 1px solid #FFE924 !important;
}

.resume{
  border: 1px solid #f5a209;
  color: #e09407;
  border-radius: calc(45px/2);
  height: 45px;
  font-size: 1.2em;
}

.resume:hover{
  background-color: #f8a710 !important;
  border: 1px solid #FFE924 !important;
  color: #fff;
}

.logo{
  display: flex;
  justify-content: center;
  align-items: center;
}

