.navBottom{
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: -1px -3px 8px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.navBottom ul li{
  display: inline;
}

.navItems{
  height: 0rem;
}

.navigation-bottom{
  position: relative;
  bottom: 0rem;
  background-color: #fff;
}

/* .navItems.show{
  height: 100%;
} */

/* .navigation .navArea{
  width: 100%;
} */

.navigation .navArea .navMenu{
  background-color: transparent;
  /* clip-path: circle(3rem at 90% 3rem); */
}

@media (max-width: 974px){
  .collapse{
    background-color: #fff;
    height: 0;
  }

  .collapse .navbar-nav{
    background-color: #fff;
    display: block !important;
    height: 0 !important;
    width: 100% !important;
    background-color: #fff;
  }

  .collapse.show{
    z-index: 15;
    background-color: #fff;
    transition: flex-grow .8s ease !important;
  }
}

.navigation-bar{
  display: block;
}

.navArea, .btnMenu, .btnClose{
  display: none;
}

@media (max-width: 479px){
  .navArea, .btnMenu, .btnClose{
    display: block;
  }

  .navigation-bar{
    display: none;
  }
}

.navigation .navArea ul li a{
  background-color: #f8a710;
}

.navArea{
  background-color: #f8a710;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  clip-path: circle(1rem at 90% 2.8rem);
  transition: clip-path .5s;
}

.btnMenu{
  position: fixed;
  top: 0.7rem;
  right: 0.35rem;
  background-color: #f8a710;
  margin-top: 0.3rem;
  margin-right: 0.6rem;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  cursor: pointer;
  padding: 1rem 1rem;
  box-shadow: -1px 3px 8px rgba(0, 0, 0, 0.2);
  z-index: 11;
}

.btnClose{
  position: absolute;
  top: 2.3rem;
  right: 1.5rem;
  background-color: transparent;
  cursor: pointer;
}

.btnClose .icon{
  background-color: #fff;
  height: 3px;
  width: 30px;
  border-radius: calc(4px/2);
  display: block;
}

.btnClose .icon:nth-child(1){
  transform: rotate(45deg);
}

.btnClose .icon:nth-child(2){
  transform: rotate(-45deg);
}

.navArea.custom{
  position: fixed;
  z-index: 15;
  clip-path: circle(100%);
}

.navArea ul{
  display: flex;
  background-color: #f8a710;
  justify-content: center;
  align-items: center;
  margin-top: 20vh;
}

.navArea ul li a{
  color: #fff;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.06rem;
}

.navArea ul li a:active{
  color: #d8d8d8;
}

.btnMenu .icon{
  background-color: #fff;
  height: 3px;
  width: 20px;
  margin-top: 0.1rem;
  margin-bottom: 0.2rem;
  border-radius: calc(4px/2);
}

.navbar-brand{
  padding: 0;
}

.navbar-brand .logo div::before{
  font-size: 2.5em;
  font-weight: bolder;
}

.navbar-brand .logo div:nth-child(2)::before{
  bottom: 0.2rem;
  left: -0.2rem;
}

.navbar-brand .logo div:nth-child(3)::before{
  left: -0.2rem;
}

.navbar-brand .logo div:nth-child(4)::before{
  left: -0.05rem;
}

.collapse ul li a{
  font-weight: normal !important;
  font-size: 1.15em;
  letter-spacing: 0.06rem;
}

@media (min-width: 975px){
  .collapse ul li a:hover{
    border-bottom: 3px solid #f5a209;
    padding-bottom: 5px;
    color: #1b1b1b;
  }
}

@media (max-width: 974px){
  .collapse.show ul li a:hover{
    color: #f5a209 !important;
    font-weight: 600 !important;
  }
}

.logo div::before{
  font-size: 5em;
  font-weight: bolder;
}

.logo div:nth-child(1)::before{
  content: "I";
  display: inline-block;
}

.logo div:nth-child(2)::before{
  content: "M";
  position: relative;
  top: -0.15rem;
  left: -0.5rem;
  display: inline-block;
  color: #f5a209;
  transform: rotate(90deg);
  -ms-transform: rotate(90deg);
}

.logo div:nth-child(3)::before{
  content: "N";
  display: inline-block;
  position: relative;
  left: -0.5rem;
}

.logo div:nth-child(4)::before{
  content: "J";
  display: inline-block;
  position: relative;
  left: -0.3rem;
}

.floating_botton button{
  position: fixed;
  right: 1rem;
  bottom: 2rem;
  width: 50px;
  height: 50px;
  padding: 0.6rem 1rem;
  border-radius: 0.5rem;
  z-index: 11;
  box-shadow: -1px 3px 8px rgba(0, 0, 0, 0.2);
  border: 1px solid #e0e0e0;
  font-size: 1.2em;
  font-weight: 100;
  color: #f5a209;
  visibility: hidden;
}

.floating_botton button:hover i{
  position: relative;
  top: 0rem;
}

@media (min-width: 751px)
{
  .floating_botton button:hover i{
    position: relative;
    top: -1.2rem;
  }

  .floating_botton button:hover::after{
    content: "Back to top";
    display: block;
    position: relative;
    top: -8rem;
    left: -3rem;
    background-color: #fff;
    border: 1px solid #ececec;
    color: #1b1b1b;
    padding: 0.5rem 0 0.5rem 0;
    width: 120px;
    font-size: 1em;
    z-index: 13;
    box-shadow: -1px 3px 8px rgba(0, 0, 0, 0.2);
    border-radius: 0.5rem;
  }
  
  .floating_botton button:hover::before{
    content: "";
    position: relative;
    top: -2.8rem;
    height: 20px;
    width: 20px;
    display: block;
    background-color: #fff;
    z-index: 14;
    transform: rotate(-45deg);
    border-left: 1px solid #ececec;
    border-bottom: 1px solid #ececec;
    border-radius: 3px;
  }
}

@media (min-width: 751px){
  .floating_botton button{
    right: 6rem;
    bottom: 5rem;
  }
}

.hero_section{
  margin-top: 2rem;
}

@media (min-width: 751px){
  .hero_section{
    margin-top: 5rem;
  }
}

.profile{
  height: 230px;
  width: 230px;
  border-radius: calc(230px/2);
  border: 2px solid #000;
  overflow: hidden;
}

@media (min-width: 751px){
  .profile{
    height: 250px;
    width: 250px;
    border-radius: calc(250px/2);
  }
}

